import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Natureza_ocorrencia } from '../../../modelo/Natureza_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'natureza_ocorrencia-dados-edit',   templateUrl: 'natureza_ocorrencia_dadosEdit.component.html' }) export class Natureza_ocorrenciaDadosEditComponent implements OnInit {    natureza_ocorrencia: Natureza_ocorrencia;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.natureza_ocorrencia.Natureza_ocorrencia";                 let id = +params['id'];         this.natureza_ocorrencia = new Natureza_ocorrencia();         this.natureza_ocorrencia.cd_natureza_ocorrencia = id;         o.objetoJson = this.natureza_ocorrencia;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a natureza_ocorrencia. ';       }      this.natureza_ocorrencia = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


